/* Global Styles */
body, html {
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  background: #f5f5f7;
  color: #333;
  scroll-behavior: smooth;
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Header and Logo */
.header {
  background: #ffffff;
  padding: 1.5rem 0;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  position: relative;
}

.logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 0.5rem;
}

.avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 4px solid #6366f1;
  margin-bottom: 1rem;
}

.header h1 {
  margin: 0.5rem 0;
  font-size: 2.5rem;
  color: #222;
  font-weight: 700;
}

.header h2 {
  margin: 0.25rem 0 1rem;
  font-size: 1.5rem;
  color: #555;
}

/* Profile Links */
.profile-links {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 0.5rem;
}

.profile-links a {
  text-decoration: none;
  color: #555;
  transition: transform 0.3s, color 0.3s;
}

.profile-links a:hover {
  color: #0070f3;
  transform: scale(1.1);
}

.profile-icon {
  font-size: 1.8rem;
}

/* Navbar */
.navbar {
  background: #ffffff;
  border-bottom: 1px solid #eaeaea;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.nav-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0.5rem;
  display: flex;
  justify-content: center;
}

.menu {
  display: flex;
  list-style: none;
  gap: 2rem;
  padding: 0;
  margin: 0;
}

.menu li a {
  text-decoration: none;
  color: #555;
  font-weight: 600;
  padding: 0.75rem 1rem;
  border-radius: 4px;
  transition: background 0.3s, color 0.3s;
}

.menu li a:hover,
.menu li a:focus,
.menu li.active a {
  background: #eaeaea;
  color: #0070f3;
}

/* Main Content */
.content {
  flex: 1;
  max-width: 1200px;
  margin: 2rem auto;
  padding: 0 1rem;
}

.section {
  margin-bottom: 2rem;
}

.section h2 {
  margin-bottom: 0.75rem;
  border-bottom: 2px solid #eaeaea;
  padding-bottom: 0.5rem;
  color: #222;
}

/* Footer */
.footer {
  background: #ffffff;
  border-top: 1px solid #eaeaea;
  text-align: center;
  padding: 1rem;
  font-size: 0.9rem;
  color: #777;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .menu {
    flex-direction: column;
    align-items: center;
  }
  .content {
    padding: 1rem;
  }
}